body {
  font-familt: 'Noto Sans'; }

.App .App-intro {
  text-align: center;
  margin: 0 auto; }

/* Position and sizing of burger button */
.bm-burger-button {
  z-index: 600 !important;
  position: fixed;
  width: 20px;
  height: 14px;
  left: 36px;
  top: 36px; }

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47; }

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px; }

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7; }

/* General sidebar styles */
.bm-menu {
  position: relative;
  top: 70px;
  background: #3D4444;
  opacity: 0.7;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
  z-index: 700; }
  .bm-menu button {
    background-color: red; }

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47; }

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em; }

/* Styling of overlay */
.bm-overlay {
  background: transparent !important; }

.menu-item {
  color: white;
  text-decoration: none;
  font-weight: normal !important; }
  .menu-item:visited {
    color: white;
    text-decoration: none; }



